export const filterPostDataAppendWebFilter = (
    wallID,
    timeStamp,
    postCount,
    networkId,
    after,
    heightEvent,
    tagName
) => {
    return (dispatch) => {

        //dispatch({ type: GET_WEB_FILTER_LOADER_SUCCESS, payload: true });
        const { wall } = store.getState().appData;
        const dataPost = store.getState().appData.postData
        const bestFit = (wall.Personalization.widgetTheme === 4 && wall.Personalization.fitRow == 1);
        var updateHeader = getHashTagParam(rID, wallID)
        if (updateHeader && updateHeader.headers && Object.keys(updateHeader.headers).length > 0) {
            if (updateHeader.headers.tag) {
                updateHeader.headers.tag = 1;
            }
            else {
                if (tagName) updateHeader.headers.tag = 1;
            }
            if (updateHeader.headers.keywords) {
                if (updateHeader.headers.keywords.length > 0) updateHeader.headers.keywords = `${updateHeader.headers.keywords}`
                else updateHeader.headers.keywords = `${updateHeader.headers.keywords}`
            }
            else {
                if (tagName) updateHeader.headers.keywords = tagName;
            }
            updateHeader.headers.action = 1;
        }
        else {
            if (tagName) {
                var headerData = { tag: 1, keywords: `${tagName}`, action: 1 }
                updateHeader.headers = headerData;
            }
            else {
                var headerData = { action: 1 }
                updateHeader.headers = headerData;
            }
        }


        if (call) call.cancel();
        call = axios.CancelToken.source();
        updateHeader.cancelToken = call.token


        dispatch({ type: GET_WEB_FILTER_LOADER_SUCCESS, payload: true });
        dispatch({
            type: FILTER_DATA_UPDATE,
            payload: {
                appendData: {
                    after: dataPost.appendData.after,
                    networkID: networkId,
                    heightEvent: heightEvent,
                    tagName: tagName
                },
                filterNetworkId: networkId,
                newData: [],
                hasMoreData: {
                    [networkId]: {
                        hasMoreData: true,
                        after: dataPost.appendData.after,
                    },
                },
                tagName
            },
        });


        var updateUrl = `${GET_DATA_POST_APPEND}${wallID}/${timeStamp}/${postCount}/${networkId}/1/${after}`
        if (isTagShop) {
            const { isStatus, mUrl } = getAttributes(rID)
            if (isStatus) updateUrl = `${updateUrl}/${mUrl}`
        }




        let urlToAccess = isTagEmbed ? `${GET_DATA_POST_APPEND}${wallID}/${postCount}/${networkId}/${after}` : updateUrl
        UseRequest().get(urlToAccess, updateHeader)
            .then((apiResponse) => {
                var response = {}
                if (isTagEmbed && !wallIdEncodeByPass.includes(wallID)) {
                    const arrayBuffer = apiResponse.data;
                    const inflatedData = pako.inflate(new Uint8Array(arrayBuffer), { to: 'string' });
                    response = { data: JSON.parse(inflatedData) };
                    if (window.location.href.includes("console=true")) console.log("api response", response)
                }
                else response = apiResponse
                const newData = response.data.data;
                dispatch({
                    type: FILTER_DATA_UPDATE,
                    payload: {
                        appendData: {
                            after: response.data.after,
                            networkID: networkId,
                            heightEvent: heightEvent,
                            tagName: tagName
                        },
                        filterNetworkId: networkId,
                        newData: newData,
                        hasMoreData: {
                            [networkId]: {
                                hasMoreData: bestFit && Object.keys(response.data.data).length < postCount ? false : response.data.hasPost == 1 ? true : false,
                                after: response.data.after,
                            },
                        },
                        tagName
                    },
                });
                dispatch({
                    type: GET_WEB_FILTER_LOADER_SUCCESS,
                    payload: false,
                });
            })
            .catch((error) => {
                dispatch({
                    type: GET_WEB_FILTER_LOADER_SUCCESS,
                    payload: false,
                });
            });
    };
};
