export const POPUP_IMAGE_RENEW_REQUEST = (event) => {
    event.persist()
    if (isTagEmbed) dataUpdate(event)
    else {
        const networkId = event.target.getAttribute("data-network")
        const post_Id = event.target.getAttribute("data-item-id");
        const themeId = event.target.getAttribute("theme-id")
        if (networkId == 1 && themeId != 3 && themeId != 4 && themeId != 16 && themeId != 47 && themeId != 50 && themeId != 55 && themeId != 60) {
            const elements = document.querySelector(`.mediaHolder${post_Id}`)
            if (elements && Object.keys(elements).length > 0) {
                elements.setAttribute('style', 'display:none;')
            }
        } else {
            if (!imageQueue.includes(event)) {
                imageQueue.push(event)
            }
            if (imageQueue.length == 1) processImageInQueue()
        }
    }
}
const processImageInQueue = async () => {
    const { postData, wall } = store.getState().appData;
    const details = wall;
    if (details && Object.keys(details).length > 0) {
        const { UserDetail, User, Wall } = details;

        const ownerId = User.id;
        const wallId = (isTagShop && dataSource == 'website') && Wall.wallId ? Wall.wallId : Wall.id;
        if (!imageQueue.length > 0) return;
        const post_Id = imageQueue[0]?.target.getAttribute("data-item-id");
        const imgTargets = document.querySelector(`img[data-item-id="${post_Id}"]`);
        const res = (postData.completeDataObject && Object.keys(postData.completeDataObject).length > 0) ? Object.values(postData.completeDataObject).filter((val) => val.id == post_Id) : [];
        const url = wall?.Wall?.url

        if (imgTargets) {
            const networkId = imageQueue[0]?.target.getAttribute("data-network")
            const filterId = imageQueue[0]?.target.getAttribute("data-filter-id");
            if ((networkId == 2 || networkId == 18 || networkId == 3 || networkId == 28) && imageQueue[0]?.target.getAttribute("data-load") == 0 && filterId != "34" && !imageQueue[0]?.target.src.includes("cloud.taggbox.com")) {
                new HttpClient().post(UPDATE_STATUS, {
                    wallId: wallId,
                    postId: post_Id,
                    table_name: UserDetail.db_table,
                    ownerId: ownerId,
                    feedId: res.length > 0 ? res[0].feedId : '',
                    type: res.length > 0 ? res[0].type : '',
                    link: res.length > 0 ? res[0].link : '',
                    postFile: res.length > 0 ? res[0].postFile : '',
                    mediaFile: res.length > 0 ? res[0].mediaFile : '',
                    url: url
                }).then(({ data }) => {
                    if (networkId == 18) {
                        const baseUrl = isTagShop || isTagEmbed ? '' : 'https://images.taggbox.com/';
                        imageQueue[0].target.src = `${baseUrl}${data.media}`;
                        imageQueue[0].target.setAttribute("data-load", "1")
                        imageQueue.shift();
                        processImageInQueue()
                    }
                    else {
                        imageQueue[0].target.src = data.media;
                        imageQueue[0].target.setAttribute("data-load", "1")
                        imageQueue.shift();
                        processImageInQueue()
                    }
                }).catch((error) => {
                    imageQueue[0].target.src = NO_IMAGE
                    imageQueue[0].target.setAttribute("data-load", "1")
                    imageQueue.shift();
                    processImageInQueue()
                })
            }
            else {
                setTimeout(() => {
                    imageQueue[0].target.src = NO_IMAGE
                    imageQueue[0].target.setAttribute("data-load", "1");
                    imageQueue.shift();
                    processImageInQueue()
                }, 500)
            }
        }
    }
}
