export const POPUP_VIDEO_RENEW = async (e, ppId) => {
    if (isTagEmbed) {
        loopUpdateDataVideo(e, ppId)
    }
    else {
        let dataVideo = document.querySelector(`.video${ppId}`) ? document.querySelector(`.video${ppId}`) : document.querySelector(".tb-detail-image-iframe")
        if (dataVideo == null || dataVideo == undefined) dataVideo = e.target;
        const { postData, wall } = store.getState().appData;
        if (dataVideo) {
            const network = dataVideo.getAttribute("data-network");
            const type = dataVideo.getAttribute("data-type");
            const wallId = (isTagShop && dataSource == 'website') && wall?.Wall?.wallId ? wall.Wall.wallId : dataVideo.getAttribute("data-wall-id");
            const postId = dataVideo.getAttribute("data-item-id");
            const filterId = dataVideo.getAttribute("data-filter-id");
            const ownerId = wall.Wall.owner ? wall.Wall.owner : dataVideo.getAttribute("data-owner-id");
            const res = (postData.completeDataObject && Object.keys(postData.completeDataObject).length > 0) ? Object.values(postData.completeDataObject).filter((val) => val.id == postId) : [];
            const url = wall?.Wall?.url
            var video_update = '';
            var image_update = '';
            const UserDetail = wall.UserDetail;

            if (network) {
                if ((network == 2 || network == 18 || network == 3 || network == 28) && dataVideo.getAttribute("data-load") != 1 && filterId != "34") {
                    if (type === 'video' && dataVideo.getAttribute("data-load") == 0 && (network == 3 || network == 28)) {
                        axios.post(UPDATE_STATUS, {
                            wallId: wallId,
                            postId: postId,
                            table_name: UserDetail.db_table,
                            ownerId: ownerId,
                            feedId: res.length > 0 ? res[0].feedId : '',
                            type: res.length > 0 ? res[0].type : '',
                            link: res.length > 0 ? res[0].link : '',
                            postFile: res.length > 0 ? res[0].postFile : '',
                            mediaFile: res.length > 0 ? res[0].mediaFile : '',
                            url: url
                        }).then(({ data }) => {

                            if (data.video != "" && e.target) e.target.src = data.video;
                            if (e.target) e.target.setAttribute("data-load", "1");
                            if (dataVideo) dataVideo.setAttribute("data-load", "1");

                        })
                        if (expirePostData && Object.keys(expirePostData).length > 0 && expirePostData[postId]) {
                            if (e.target) {
                                e.target.src = expirePostData[postId].video;
                                e.target.setAttribute("data-load", "1")
                            }
                            else if (dataVideo) {
                                dataVideo.src = expirePostData[postId].video;
                                dataVideo.setAttribute("data-load", "1")
                            }
                        }
                    }
                    else {
                        if (type === 'video' && dataVideo.getAttribute("data-load") == 0 && (network == 3 || network == 28)) {
                            UseRequest().post(UPDATE_STATUS, {
                                wallId: wallId,
                                postId: postId,
                                table_name: UserDetail.db_table,
                                ownerId: ownerId,
                                feedId: res.length > 0 ? res[0].feedId : '',
                                type: res.length > 0 ? res[0].type : '',
                                link: res.length > 0 ? res[0].link : '',
                                postFile: res.length > 0 ? res[0].postFile : '',
                                mediaFile: res.length > 0 ? res[0].mediaFile : '',
                                url: url
                            }).then(({ data }) => {
                                expirePostData[postId] = { postId: postId, video: data.video, image: data.media }
                                if (data.video != "") e.target.src = data.video;
                                e.target.attributes["data-load"].value = "1";
                                e.target.setAttribute("data-load", "1")
                            })
                        }
                        else if (type === 'video' && (network == 2 || network == 18) && dataVideo.getAttribute("data-load") != 1) {
                            const dataSrc = dataVideo.src;
                            let links = dataVideo.getAttribute("data-link");
                            let postIdNew = links.split('/');
                            let fetchUrl = `https://images${~~(Math.random() * 3333)}-focus-opensocial.googleusercontent.com/gadgets/proxy?container=none&url=${links}`;
                            let IMG_URL_UPDATE = await FETCH_LINK(fetchUrl, postIdNew, dataSrc, 'video')
                            if (IMG_URL_UPDATE && IMG_URL_UPDATE != null && IMG_URL_UPDATE.length > 0 && typeof IMG_URL_UPDATE === 'string' && IMG_URL_UPDATE.includes("no-image.svg")) {
                                UseRequest().post(UPDATE_STATUS, {
                                    wallId: wallId,
                                    postId: postId,
                                    table_name: UserDetail.db_table,
                                    ownerId: ownerId,
                                    feedId: res.length > 0 ? res[0].feedId : '',
                                    type: res.length > 0 ? res[0].type : '',
                                    link: res.length > 0 ? res[0].link : '',
                                    postFile: res.length > 0 ? res[0].postFile : '',
                                    mediaFile: res.length > 0 ? res[0].mediaFile : '',
                                    url: url
                                }).then(({ data }) => {
                                    if (data.video != "") dataVideo.src = data.video;
                                    video_update = data.video;
                                    image_update = data.media;
                                    expirePostData[postId] = { postId: postId, video: data.video, image: data.media }
                                    dataVideo.setAttribute("data-load", "1")
                                })
                            }
                            else {
                                if (IMG_URL_UPDATE && IMG_URL_UPDATE != null && IMG_URL_UPDATE.length > 0 && typeof IMG_URL_UPDATE === 'string') {
                                    dataVideo.src = (window.navigator.userAgent.includes('Safari') && !window.navigator.userAgent.includes('Chrome')) ? `https://cdn.taggbox.com/v7/${IMG_URL_UPDATE}` : `https://images.taggbox.com/${IMG_URL_UPDATE}`;
                                    video_update = IMG_URL_UPDATE;
                                    dataVideo.attributes["data-load"].value = "1";
                                    dataVideo.setAttribute("data-load", "1")
                                    if (IMG_URL_UPDATE.includes("no-camera.svg")) {
                                        //dataVideo.style = "visibility:visible!important;width:180px;margin:20px auto;object-fit: contain;";
                                    }
                                    //dataVideo.style = "";
                                    if (!video_update.includes("no-image.svg") && !video_update.includes("no-camera.svg") && !video_update.includes("cdn.taggbox.com") && !video_update.includes("cloud.taggbox.com")) {
                                        updateInstagramPostImage({
                                            wallId: wallId,
                                            postFile: image_update,
                                            video: video_update,
                                            type: 5,
                                            postId: postId,
                                            ownerId: ownerId
                                        });
                                    }
                                }
                                else if (typeof IMG_URL_UPDATE === 'object' && type === 'video') {
                                    dataVideo.src = `https://images.taggbox.com/${IMG_URL_UPDATE.video_url}`;
                                    video_update = IMG_URL_UPDATE.video_url;
                                    if ((window.navigator.userAgent.includes('Safari') && !window.navigator.userAgent.includes('Chrome'))) {
                                        dataVideo.setAttribute("src", `https://cdn.taggbox.com/v7/${IMG_URL_UPDATE.video_url}`);
                                    }
                                    else {
                                        dataVideo.setAttribute("src", `https://images.taggbox.com/${IMG_URL_UPDATE.video_url}`);
                                    }

                                    dataVideo.setAttribute("data-load", "1");
                                    if (IMG_URL_UPDATE.display_url.includes("no-camera.svg")) {
                                    }
                                    if (!video_update.includes("no-image.svg") && !video_update.includes("no-camera.svg") && !video_update.includes("cdn.taggbox.com") && !video_update.includes("cloud.taggbox.com")) {
                                        updateInstagramPostImage({
                                            wallId: wallId,
                                            postFile: image_update,
                                            video: video_update,
                                            type: 5,
                                            postId: postId,
                                            ownerId: ownerId
                                        });
                                    }

                                }
                            }
                        }
                    }
                }
                else {
                    dataVideo.setAttribute("data-load", "1")
                }
            }
        }
    }
}
