export const SocialFonts = () => {
    const version = 5
    const WebFonts = `@font-face { font-family: 'WebSocialFonts'; src: url('${SocialCloudUrl}/web-fonts/fonts-icon/WebFonts.ttf?var=${version}') format('truetype'), url('${SocialCloudUrl}/web-fonts/fonts-icon/WebFonts.woff?var=${version}') format('woff'), url('${SocialCloudUrl}/web-fonts/fonts-icon/WebFonts.svg?var=${version}') format('svg'); font-weight: normal; font-style: normal; font-display: swap;`
    SocialCustomStyle(WebFonts)
}

export const loadSiteFeed = () => {
    if (addfeedload) {
        if (!messageSent) {
            SocialFonts()
            const newData = { url: window.location.href }
            window.parent.postMessage({ type: "reloadWebsite", data: JSON.stringify(newData) }, "*");
            messageSent = true;
        }
    }
}
export const onsiteSetting = (dispatch, Id) => {
    try {
        //onsite_status
        UseRequest().get(`${ONSITE_SETTING}${Id}`)
            .then((apiResponse) => {
                const { responseData } = apiResponse.data;
                const { UgcSettings, PhotoBoothSettings } = responseData;
                if (window.location.href.includes("onsite-upload") && Object.keys(UgcSettings).length == 0) {
                    UgcSettings['onsite_status'] = 0
                } 
                if (window.location.href.includes("onsite-upload") && Object.keys(PhotoBoothSettings).length == 0) {
                    PhotoBoothSettings['photo_booth_status'] = 0;
                }                 
                if (Object.keys(responseData).length > 0) dispatch({ type: UGC_SETTING_UPDATE, payload: responseData, wallId: Id })
                dispatch({ type: ONSITE_TOKEN_UPDATE, payload: responseData?.onsite_token })
                dispatch({ type: ONSITE_POPUP, payload: true })

            })
            .catch((error) => {

            });
    }
    catch (ex) {
        const { response } = ex;
        dispatch({
            type: ERROR_RESPONSE_WITH_MESSAGE,
            payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response.data },
        });
        throw new Error("");
    }
};
